








































































































































































































































@import './styles/colors';

#navbar {
  display: flex;
  flex-direction: column;

  height: 100%;
  width: 300px;
  background-color: $color-purple;
  position: fixed;
  z-index: 100;
}

#content {
  margin-left: 300px;
}

.oslo__content {
  display: flex;
  flex-wrap: wrap;
  height: 100vh;
  max-height: 100%;
  padding-left: 300px;
  position: relative;
}

.oslo__logo {
  cursor: pointer;
  margin-top: 3rem;
  margin-bottom: 2rem;
  align-self: center;
}

.oslo__navigation-drawer {
  background-color: $color-purple;
  display: flex;
  flex-direction: column;
}

.oslo__navigation-link {
  $p: &;

  position: relative;
  background-color: $color-purple;
  color: rgba(white, 0.9);
  transition: all 0.3s ease-in-out;
  display: flex;
  align-items: center;

  cursor: pointer;
  width: 100%;

  &--active {
    background-color: $color-blue;
    transition: all 0.3s ease-in-out;

    #{$p}--label {
      color: $color-purple;
      font-weight: 500;
    }

    label:hover {
      background: none !important;
    }

    input[type='checkbox']:checked + label {
      &::after {
        transform: scale(0);
      }
    }
  }

  &--compare {
    background-color: lighten($color-purple, 5%);
    transition: background-color 0.3s ease-in-out;
  }

  &--label {
    letter-spacing: 0.3px;
    display: flex;
    flex-grow: 1;
    height: 40px;
    align-items: center;
    position: relative;

    &-compare {
      margin-bottom: 2rem;
      margin-top: 2rem;
      padding-left: 3.6rem;
    }

    // Add visual border on the left side of text on hover
    &::before {
      content: '';
      display: none;
      position: absolute;
      width: 10px;
      top: 0;
      left: -10px;
      bottom: 0;
      background: $color-purple;
      pointer-events: none;
    }
  }

  &:not(&--active) &--label:hover {
    background-color: darken($color-purple, 5%);

    // Show the visual border on hover
    &::before {
      background-color: darken($color-purple, 5%);
      display: block;
    }
  }
}

.oslo__navigation-topbar {
  background-color: white;
  border-bottom: 1px solid $color-grey-300;
  display: flex;
  flex-direction: column;
  padding: 2rem;
  width: 100%;

  &-button {
    display: flex;
    flex-direction: row;
  }
}

.oslo__topbar {
  color: $color-purple;
  font-weight: bold;
  margin-bottom: 2rem;

  &-text {
    letter-spacing: 0.8px;
    margin-left: 1rem;
    margin-top: 0.3rem;
  }
}

.v-input--selection-controls {
  margin-top: 0.6rem;
  padding-top: 0;
}

.v-select__selection--uppercase {
  font-size: 24px;
  font-weight: bold;
  letter-spacing: 0.7px;
  text-transform: uppercase;
}

input[type='checkbox'] {
  display: none;

  & + label {
    cursor: pointer;
    display: inline-block;
    height: 40px;
    position: relative;
    vertical-align: middle;
    width: 50px;
    padding-left: 1rem;

    &:hover {
      background: darken($color-purple, 10%);
      &::before {
        opacity: 0.8;
        background: rgba(white, 0.15);
      }
    }

    // checked inside border
    &::after {
      background-color: $color-blue;
      border-radius: 1px;
      content: '';
      height: 10px;
      left: 24px;
      position: absolute;
      top: 14px;
      width: 10px;
      transform: scale(0);
      transition: all 0.3s ease-in-out;
    }

    // unchecked border
    &::before {
      -moz-border-radius: 1px;
      -webkit-border-radius: 1px;
      border: 1px solid white;
      border-radius: 1px;
      content: '';
      height: 18px;
      left: 20px;
      opacity: 0.35;
      position: absolute;
      top: 10px;
      width: 18px;
      transition: all 0.3s ease-in-out;
    }
  }

  &:checked + label {
    position: relative;
    opacity: 1;

    // checked inside border
    &::after {
      transform: scale(1);
      transition: transform 0.3s cubic-bezier(0.29, -0.01, 0.41, 1.9);
    }

    // checked border
    &::before {
      border: 1px solid $color-blue;
      opacity: 1;
      transition: all 0.3s cubic-bezier(0.29, -0.01, 0.41, 1.9);
    }
  }
}

.oslo__navigation-drawer__button-container {
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  padding-right: 1rem;
}

.button-container {
  align-self: flex-end;

  .oslo__navigation-button {
    border: 1px solid rgb(248, 198, 107);
    color: rgb(248, 198, 107);
    padding: 0.3rem 1rem 0.3rem 1rem;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
    margin: 0.5rem;
    transition: all 0.3s ease-in-out;

    &:disabled {
      opacity: 0.2;
      transition: opacity 0.3s ease-in-out;
    }

    &:hover:not([disabled]) {
      box-shadow: 0 0 0 1px #e0e0e0;
    }
  }
}
